  --offset-42: 2.625rem;
  --offset-40: 2.5rem;
  --offset-38: 2.375rem;
  --offset-36: 2.25rem;
  --offset-32: 2rem;
  --offset-30: 1.875rem;
  --offset-28: 1.75rem;
  --offset-26: 1.625rem;
