.basket-total {
  grid-column: 8/-1;
}
@media (max-width: 1024px) {
  .basket-total {
    grid-column: 1/-1;
  }
}

.summary {
  max-width: 27.5rem;
  padding: 3.125rem 1.25rem;
  background-color: #E6DCD3;
}
@media (max-width: 1024px) {
  .summary {
    padding: 2.4375rem 0.9375rem 2.4375rem 0.8125rem;
  }
}
@media (max-width: 1024px) {
  .summary h3:first-child {
    font-size: 1.625rem;
    line-height: 1.980625rem;
  }
}
.summary p {
  padding-bottom: 0.46875rem;
  border-bottom: 0.03125rem solid #898989;
}
.summary p:first-of-type {
  margin-top: var(--offset-32);
}
.summary p:not(:first-of-type) {
