    line-height: 1.82875rem;
  }
}

.summary-checkout {
  background-color: #3F352C;
  transition: background-color 200ms ease-in-out;
  padding: 0.625rem;
  margin-top: var(--offset-55);
  color: #FFF;
}
@media (hover: hover) {
  .summary-checkout:hover, .summary-checkout:focus {
    background-color: #BDA18C;
  }
}
@media (max-width: 1024px) {
