}
.basket-products__item .loader {
  width: 4.375rem;
  height: 4.375rem;
}
.basket-products__item.loading {
  pointer-events: none;
}
.basket-products__item.loading .loader-wrapper {
  visibility: visible;
}
.basket-products__item.loading .loader {
  animation: l13 1s infinite linear;
}
.basket-products__item.loading .overlay {
  opacity: 0.78;
}
.basket-products__item > .basket-products__wrapper {
  display: none;
}
@media (max-width: 1024px) {
  .basket-products__item > .basket-products__wrapper {
    display: block;
    margin-top: 2rem;
    margin-left: 8.125rem;
