    line-height: 1.52375rem;
  }
}

.basket-products__inner {
  display: contents;
}
@media (max-width: 1024px) {
  .basket-products__inner {
    display: flex;
    gap: 0.75rem;
    margin-top: 0.5rem;
  }
}

.basket-products__thumb {
