    height: 9.375rem;
  }
}

@media (max-width: 1024px) {
  .basket-products__title {
    font-size: 1.25rem;
    line-height: 1.52375rem;
  }
}

.basket-product__name {
  margin-top: 1.375rem;
  font-size: 1.375rem;
  line-height: 1.375rem;
}
@media (max-width: 1024px) {
  .basket-product__name {
    font-size: 1rem;
  }
}

.basket-products__size {
  margin-top: 1.375rem;
}

.basket-products__wrapper span:first-child {
  margin-right: var(--offset-22);
}

.counter-wrapper {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}
@media (max-width: 1024px) {
  .counter-wrapper {
    display: block;
  }
}
@media (max-width: 1024px) {
  .counter-wrapper .basket-products__wrapper {
    display: none;
  }
}

.counter {
  display: flex;
  align-items: center;
  max-width: fit-content;
  height: 2rem;
  border: 0.0625rem solid #000;
}
@media (max-width: 1024px) {
  .counter {
    height: 1.5625rem;
    display: flex;
    align-items: flex-end;
  }
}

@media (max-width: 1024px) {
  .counter__value {
    font-size: 1.125rem;
    line-height: 1.37125rem;
  }
}

.counter__button {
  box-sizing: content-box;
  width: 1.0625rem;
  height: 1.0625rem;
  padding: 0.4375rem 1rem 0.375rem;
}
@media (max-width: 1024px) {
  .counter__button {
    width: 0.875rem;
    height: 0.875rem;
    padding: 0.3125rem 0 0.25rem;
  }
}
@media (max-width: 1024px) {
  .counter__button:first-of-type {
    padding-left: 0.75rem;
    padding-right: 1.125rem;
  }
}
.counter__button:first-of-type .counter__icon {
  width: 1.0625rem;
  height: 1.0625rem;
}
@media (max-width: 1024px) {
  .counter__button:first-of-type .counter__icon {
    width: 0.875rem;
    height: 0.875rem;
  }
}
@media (max-width: 1024px) {
  .counter__button:last-of-type {
    padding-left: 1.125rem;
    padding-right: 0.75rem;
  }
}
.counter__button:last-of-type .counter__icon {
  width: 1.0625rem;
  height: 0.0625rem;
}
@media (max-width: 1024px) {
  .counter__button:last-of-type .counter__icon {
    width: 0.875rem;
  }
}

.basket-products__delete {
  position: absolute;
}
@media (max-width: 1024px) {
  .basket-products__delete {
    position: static;
    display: block;
    margin-left: auto;
  }
}

.basket-products__icon {
  width: 1.5rem;
  height: 1.5rem;
}

.empty-basket {
  grid-column: 3/-3;
  padding-top: 2.375rem;
  padding-bottom: 2.0625rem;
  border: 0.0625rem solid #BABABA;
}
@media (max-width: 1024px) {
  .empty-basket {
    grid-column: 1/-1;
    padding-top: 1.5625rem;
    padding-bottom: 4.1875rem;
  }
}

.empty-basket__thumb {
  width: 17.125rem;
  height: 16rem;
}

.empty-basket__title {
  margin-top: var(--offset-40);
}

.empty-basket__text {
  margin-top: 0.1875rem;
}
@media (max-width: 1024px) {
  .empty-basket__text {
    font-size: 1rem;
    line-height: 1.21875rem;
  }
}

.products__item {
  transition: opacity 150ms ease-in-out;
}
.products__item .loader {
  width: 5.0625rem;
  height: 5.0625rem;
}
@media (max-width: 1024px) {
  .products__item .loader {
    width: 3.3125rem;
    height: 3.3125rem;
  }
}
.products__item .loader-wrapper {
  position: absolute;
  top: 19.125rem;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 1024px) {
  .products__item .loader-wrapper {
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
.products__item.loading {
  pointer-events: none;
}
.products__item.loading .loader-wrapper {
  visibility: visible;
}
.products__item.loading .loader {
  animation: l13 1s infinite linear;
}
.products__item.loading .overlay {
  opacity: 0.78;
}

.products__link {
  display: inline-block;
  padding-bottom: var(--offset-28);
}
@media (hover: hover) {
  .products__link:hover + .products__buy, .products__link:focus + .products__buy {
    background-color: #BDA18C;
  }
  .products__link:hover ~ .products__checkout, .products__link:focus ~ .products__checkout {
    color: #FFF;
    background-color: #3F352C;
  }
}

.products__thumb {
  height: 31.25rem;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: 200% 200%;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .products__thumb {
    height: 13rem;
  }
}
@media (hover: hover) {
  .products__thumb:hover .products__image, .products__thumb:focus .products__image {
    opacity: 0;
  }
}

.products__image {
  transition: opacity 200ms ease-in-out;
}

.products__title {
  margin-top: var(--offset-22);
}
@media (max-width: 1024px) {
  .products__title {
    font-size: 1.125rem;
    line-height: 1.37125rem;
  }
}

.products__size {
  margin-top: var(--offset-13);
}

.products__price {
  margin-top: var(--offset-23);
}
@media (max-width: 1024px) {
  .products__price {
    display: flex;
    justify-content: space-between;
