}

.products__thumb {
  height: 26.5625rem;
}

@media (max-width: 1024px) {
  .products__title {
    margin-top: 1.375rem;
    font-size: 1.25rem;
    line-height: 1.52375rem;
  }
}

@media (max-width: 1024px) {
  .products__price {
    margin-top: 1.125rem;
    justify-content: flex-start;
  }
}
@media (max-width: 1024px) {
  .products__price span:first-child {
